import {AContainer} from "./abstract/a-container";
import {IRenderable} from "../core/interface/i-renderable";
import {Box} from "./box";
import jquery = require("jquery");
/**
 * 
 * container para exibição de código fonte.
 * 
 * @memberof module:mvcomponents/container
 * 
 * @example
 *  var contCod:CodeContainer = new CodeContainer();
 *  contCod.setCodeText("<span>123131</span>");  
 */
export class CodeContainer extends AContainer<Box> {
	constructor() {
		super("div", `<pre class="pre-cod"></pre>`);
	}
	public setCodeText(codeText:string):this{
		jquery(this.element)
			.find("pre.pre-cod:first")
			.text(codeText);
		return this;
	}
}
